@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@100;200;300;400;600&family=Poppins:wght@300;400&display=swap');

body {
  font-family: 'Poppins', sans-serif !important;
}

/* Nav Bar Css  */

.nav-link {
  color: #ffffff !important;
}

.navbar {
  /* background-color: black !important; */
}

.nav-link:hover {
  color: #20C997 !important;
}

.font-icon {
  color: white;
}


/* homepage CSS  */

.masthead {
  position: relative;
  background-image: url(./assets/images/intro-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: 50%;
  min-height: 100vh;
  /* height: 100%;
  width: 100%; */
  z-index: -2;

}

.colo-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* background-color: rgba(50, 50, 50, 0.363); */
  background-color: #111418;
  opacity: .8;
  z-index: -1;
}

.main-section {
  margin-top: -400px;
}

.btn-hire {
  color: #20C997;
  border: 2px solid #20C997;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
  padding: 0.8rem 2.6rem;
}

.btn-hire:hover {
  background-color: #20C997;
  color: #ffffff;
  transition: all 0.5 ease;
}

.arrow-margin {
  height: 100px;
  margin-top: 90px;

}

.arrow-animation {
  /* position: relative; */
  animation: fadeInDown 1.5s infinite;
}

/* About CSS  */
.text-light {
  color: #dee3e4 !important;
}

.opacity-4 {
  opacity: 0.4;
}

.fw-600 {
  font-weight: 600 !important;
}

.text-24 {
  font-size: calc(1.95rem + 8.4vw) !important;
}

.text-9 {
  font-size: 2.25rem !important;
}

.border-primary {
  border-color: #20C997 !important;
}

.heading-separator-line {
  width: 80px;
}

.text-7 {
  font-size: 1.75rem !important;
}

.text-primary {
  color: #20C997 !important;
}

p {
  line-height: 1.8;
}

ul {
  margin-bottom: 1rem;
  margin-top: 0;
  display: block;
  list-style-type: disc;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 40px;
}

.list-style-2 {
  padding: 0;
}

.list-style-2>li {
  border-bottom: 1px solid #eaeaea;
  list-style-type: none;
  padding-bottom: 12px;
  padding-top: 12px;
}

.email-link {
  color: #20C997;
  text-decoration: none;
  transition: all 0.2s ease;
}

.btn {
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
  padding: 0.8rem 2.6rem;
}

.btn:hover {
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  transition: all 0.5s ease;
  color: #ffffff;
  background-color: #20C997;
  border-color: #20C997;
}

.btn-primary {
  background-color: #20C997;
  border-color: #20C997;
}

.featured-box {
  box-sizing: border-box;
  position: relative;
}

.featured-box h4 {
  font-weight: 500;

}

.text-12 {
  font-size: 3rem !important;
  line-height: 1.3;
}

.section {
  overflow: hidden;
  padding: 4.5rem 0;
  position: relative;

}

.featured-box.style-3 {
  padding-left: 90px;
  padding-top: 0px;
}

.featured-box-icon {
  align-items: center;
  justify-content: center;
  font-size: 30px;
  left: 0;
  margin-bottom: 0;
  position: absolute;
  text-align: center;
  top: 0;
  display: flex;
  height: 70px;
  width: 70px;
  flex-shrink: 0;
}

.text-6 {
  font-size: 1.5rem !important;
}

.bg-primary {
  background-color: #20C997 !important;
}

.progress-bar {
  background-color: #20C997;
}

.btn-outline-secondary:hover {
  background-color: #6c757d;
  border-color: #6c757d;
  color: #ffffff;
}

.nav-item .nav-link .active {
  color: #20C997;
}

.nav-tabs .nav-item .nav-link {
  background: transparent;
  border: 0;
  border-radius: 0;
  /* color: #7b8084 !important; */
  cursor: pointer;
  padding: 0.6rem 1rem;
  position: relative;
  white-space: nowrap !important;
}

.text-3 {
  font-size: 1rem !important;
}

.text-4 {
  font-size: 1.125rem !important;
}

.social-icons {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
}

.social-icons li {
  margin: 2px 6px;
  overflow: visible;
  padding: 0;
}

.social-icons li a {
  color: #4d555a;
  display: block;
  font-size: 18px;
  height: 26px;
  line-height: 26px;
  text-align: center;
  text-decoration: none;
  transition: all .2s ease;
  width: 26px;
}

#footer .nav .nav-item {
  display: inline-block;
  line-height: 12px;
  margin: 0;
}

#back-to-top {
  background-color: rgba(0, 0, 0, .2);
  border-radius: 50%;
  bottom: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, .15);
  color: #fff;
  cursor: pointer;
  display: none;
  font-size: 18px;
  height: 44px;
  line-height: 42px;
  position: fixed;
  right: 10px;
  text-align: center;
  transition: all .3s ease-in-out;
  width: 44px;
  z-index: 1030;
}

.footer-ajay {
  text-decoration: none !important;
}

.navbar-toggler {
  background-color: #eaeaea;
}

.rounded-circle {
  border-radius: 50% !important;
}

/* .icon-arrow {
  background-color: rgba(0, 0, 0, .2) !important;
} */

/* Animations */

@keyframes fadeInDown {
  from {
    opacity: 0;
    margin-top: 0;
  }

  to {
    opacity: 1;
    margin-top: 10px;
  }
}


/* Media  */

@media (min-width: 992px) {
  .ps-lg-4 {
    padding-left: 1.5rem !important;
  }
}